import React from 'react'
import { Typography, Card } from 'antd'
import ModalImage from "react-modal-image";
import {Link} from 'gatsby';
import LandingLayout from '@layouts/LandingLayout'


export default () => (
  <LandingLayout activeTab = '2' lState={true}>
    <div className='flex flex-col items-start'>
     <Card 
     style={{ width: '100%' }}
     title="Dev Notes: First Release"
     extra={<Link to="/projects/securet/1">Read more</Link>}
     >
     Aug 5, 2020 <br/> This is the first release for this app. I intended this app to be a simple 
     experement but I hope that it will be impactful.  
     </Card>
    </div>
  </LandingLayout>
)
